<template>
  <div class="register flex">
    <div class="swiper">
      <div v-show="num === index" @mouseleave="start" v-for="(item, index) in img" :key="index"><img :src="item" alt="" /></div>
    </div>
    <div class="box">
      <h2 class="title">欢迎注册music</h2>
      <div class="may">每一天，乐在沟通。</div>
      <el-form :model="register" :rules="rules" ref="ruleForm" class="registerFrom">
        <el-form-item prop="nickname">
          <el-input v-model="register.nickname" placeholder="昵称"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model="register.password" placeholder="密码" type="password"></el-input>
        </el-form-item>
        <el-form-item> <el-input v-model="register.phone" placeholder="手机号" @focus="focus"></el-input> </el-form-item>
        <el-form-item class="test" v-show="isshow">
          <el-input v-model="register.captcha" placeholder="短信验证码" class="test-code"></el-input>
          <el-button class="bnt" @click.stop="sendCaptcha" v-show="isCaptcha">发送短信验证码</el-button>
          <el-button class="bnt" v-show="!isCaptcha" disabled>{{ time }}秒后重新获取</el-button>
        </el-form-item>
        <el-button class="registerBnt" @click="registerBnt">立即注册</el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
import { sendCaptchaCode, verify, checkPhone, register } from '@/network/login'
export default {
  data () {
    return {
      register: {
        nickname: '',
        password: '',
        phone: '',
        captcha: ''
      },
      num: 0,
      img: [require('@/assets/img/re/01-1.jpg'), require('@/assets/img/re/01-2.jpg'), require('@/assets/img/re/01-3.jpg'), require('@/assets/img/re/01-4.jpg')],
      rules: {
        nickname: [
          { required: true, message: '请输入昵称', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 5, max: 15, message: '长度在 5 到 15 个字符', trigger: 'blur' }
        ]
      },
      isshow: false,
      time: 59,
      isCaptcha: true
    }
  },
  methods: {
    start () {
      this.timer = setInterval(() => {
        this.num += 1
        if (this.num === this.img.length) {
          this.num = 0
        }
      }, 10000)
    },
    focus () {
      this.isshow = true
    },
    sendCaptcha () {
      sendCaptchaCode(this.register.phone).then((res) => {
        this.isCaptcha = false
        var timer = setInterval(() => {
          this.time--
          if (this.time <= 0) {
            this.isCaptcha = true
            clearInterval(timer)
          }
        }, 1000)
      })
    },
    registerBnt () {
      // 验证验证码
      verify(this.register.phone, this.register.captcha).then((res) => {
        console.log(res)
      })
      // 检测手机号码是否已注册
      checkPhone(this.register.phone).then((res) => {
        console.log(res)
        if (res.data.hasPassword === true && res.data.nickname !== '') return this.$message.error('手机号已经注册')
      })
      // 注册
      register(this.register.phone, this.register.captcha, this.register.password, this.register.nickname).then(res => {
        console.log(res)
      })
    }

  },
  mounted () {
    this.start()
  }
}
</script>

<style scoped lang="less">
.register {
  height: 698px;
}
.swiper img {
  height: 698px;
}
.box {
  width: 400px;
  position: absolute;
  top: 150px;
  right: 350px;
  .title {
    font-size: 30px;
    font-weight: 500;
    padding: 20px 0;
  }
  .may {
    font-size: 20px;
    font-weight: 500;
  }
  .registerFrom {
    margin-top: 50px;
    width: 100%;
    .test {
      display: flex;

      .test-code {
        width: 110px;
      }
      .bnt {
        width: 210px;
        margin-left: 80px;
      }
    }
    .registerBnt {
      width: 400px;
    }
  }
}
</style>
